import question from '@/static/mbcImg/home/extendManageList/question.png';
import { mapMutations , mapGetters} from 'vuex';
export default {
	name: 'account',
	components: {
	},
	data() {
		return {
			question: question,
			loadingShow: false, // loading
			accountData: '', // 个人账户信息数据
			confirmShow: false,
			confirmBtn: '知道了',
			confirmTitle: '什么是可用余额？',
			confirmText: '可用余额：即账户余额减去冻结金额后的余额。',
			availableAmount: 0, // 可用余额
			UserData: {} ,// 用户信息全部
			list:[],
		};
	},
	props: {
		// 列表菜单
		listData: {
			type: Object,
			default: function(e) {
				return {};
			}
		}
	},
	created() {
		this.moneys();
	},
	computed: {
		i18n () {
		  return this.$t('extendManageList')  
		},
	},
	methods: {
		...mapMutations({}),
		clickQuickBtn() {
			this.$store.commit('setQuickNavShow', true);
			console.log('触发快捷导航按钮');
		},
		onConfirm(msg) {
			console.log('on confirm');
			if (msg) {
				alert(msg);
			}
		},
		questions(e){
			if(e===2){
				uni.showModal({
					title: '什么是冻结余额？',
					content: '冻结金额：即账户开通时需缴纳的金额，一个账户只会有一个冻结金额。',
					confirmColor:' #02C2A2',
					showCancel:false,
					confirmText:'知道了',
					success: function (res) {
						
					}
				});
			}else if(e===1){
				uni.showModal({
					title: '什么是可用余额？',
					content: '可用余额：即账户余额减去冻结金额后的余额。',
					confirmColor:' #02C2A2',
					showCancel:false,
					confirmText:'知道了',
					success: function (res) {
						
					}
				});
			}
		},
		moneys(){
			if (uni.getStorageSync('landRegist')) {
				let landRegistLG = JSON.parse(uni.getStorageSync('landRegist')); // 读取缓存的用户信息
				console.log(landRegistLG.user.id);
				let params = {}; // 请求总数居时 参数为空
				uni.showLoading({ // 展示loading
					title: '加载中'
				});
				uni.request({
					url: this.api2 + '/rest-rp/compAccount/760', //接口地址。
					// data: this.endParams(params),
					method: 'GET',
					header: {
						Authorization: "Bearer " + landRegistLG.token //将token放到请求头中
					},
					success: (response) => {
						uni.hideLoading();
						console.log(response.data);
						this.list = response.data.content
						// this.$store.commit('setMation', this.List); // 更新vuex
						
					},
					fail: (error) => {
						uni.hideLoading(); // 隐藏 loading
						uni.showToast({
							title: '网络繁忙，请稍后',
							icon: 'none',
							duration: 1000
						});
						console.log(error, '网络繁忙，请稍后');
					}
				});
			}
		},
	}
};
